@import url(https://fonts.googleapis.com/css2?family=Urbanist:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,100;1,200;1,300;1,400&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  --font1:'Urbanist';
}
body,html{
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 48
}
.navbar span{
  display: none;
}
a{
  text-decoration: none;
  /* background-color: aqua; */
  color: #000;
}

.navbar{
  position: fixed;
  width: 100%;
  z-index: 100;
  height: 70px;
  /* background-color: rgb(128, 7, 7); */
  padding: 20px 20px  10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* margin-top: 1vmax; */
  box-shadow: 1px 1px 20px 1px;
  background-color: white;
  /* transform: translateY(100%); */
}
.navl{
  flex:1 1;
  display: flex;
  
}
.navbar h3{
  font:400 1.2vmax var(--font1) ;
  margin: 0 1vmax;
}
.navm{
  flex:1 1;
  text-align: center;
  font:500 2vmax var(--font1) ;
}
.navr{
  flex:1 1;
  text-align: center;
  display: flex;
  justify-content: flex-end;
}


.naval, .navar{
  width: 70px;
  height: 70px;
  font-size: 2vmax;
  padding: 1vmax;
  border-radius: 50px;
  background-color: aqua;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  cursor: pointer;
  opacity: 50%;
  z-index: 80;
  display: none;
}
.naval{
  position: absolute;
  left: 10px;
}
.navar{
  position: absolute;
  right: 10px;
}
.slider{
  width: 100%;
  height: 70vmin;
  padding-top: 2vmax;
  display: flex;
  justify-content: center;
  /* background-color: #cf8a59; */
  overflow: scroll;
}
.slide-wrapper{
  width: 80%;
  height: 100%;
  overflow: visible;

}
.slidecont{
  display: flex;
  -webkit-animation: animation 18s ease-in-out  infinite ;
          animation: animation 18s ease-in-out  infinite ;
  /* transition: all 1s ease-in-out; */
  /* overflow: scroll; */
  /* transform: translateX(-80vw); */
  /* transform: translateX(-160vw); */
  /* transform: translateX(-240vw); */
}
@-webkit-keyframes animation {
  0%   {-webkit-transform: translateX(0vw);transform: translateX(0vw);}
  20%   {-webkit-transform: translateX(0vw);transform: translateX(0vw);}
  25%  {-webkit-transform: translateX(-80vw);transform: translateX(-80vw);}
  45%  {-webkit-transform: translateX(-80vw);transform: translateX(-80vw);}
  50%  {-webkit-transform: translateX(-160vw);transform: translateX(-160vw);}
  75%  {-webkit-transform: translateX(-160vw);transform: translateX(-160vw);}
  80% {-webkit-transform: translateX(-240vw);transform: translateX(-240vw);}
  100% {-webkit-transform: translateX(-240vw);transform: translateX(-240vw);}
}
@keyframes animation {
  0%   {-webkit-transform: translateX(0vw);transform: translateX(0vw);}
  20%   {-webkit-transform: translateX(0vw);transform: translateX(0vw);}
  25%  {-webkit-transform: translateX(-80vw);transform: translateX(-80vw);}
  45%  {-webkit-transform: translateX(-80vw);transform: translateX(-80vw);}
  50%  {-webkit-transform: translateX(-160vw);transform: translateX(-160vw);}
  75%  {-webkit-transform: translateX(-160vw);transform: translateX(-160vw);}
  80% {-webkit-transform: translateX(-240vw);transform: translateX(-240vw);}
  100% {-webkit-transform: translateX(-240vw);transform: translateX(-240vw);}
}

.slide{
  width: 80vw;
  height: 70vmin;
  display: flex;
  justify-content: center;
  align-items: center;
/* border-style: solid; */
/* position: relative; */
}
.img-cont{
  width: 50%;
  height: 100%;
}
.info-cont{
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.slideimg{
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.slide h3{
  font: 400 3vmax var(--font1);
}
.slide p{
  font: 300 1.2vmax var(--font1);
  margin: 30px 0;
}
.slide button{
  font: 300 1.2vmax var(--font1);
  padding: 1vmax 2vmax;
}


.slide-wrapper{
  display: flex;
  overflow: hidden;
  /* transform: translateX(-50vw); */
}


